.CategoryItem {
    user-select: none;
    cursor: pointer;
    transition: all 150ms linear;
    border-right: 1px solid;
    border-right-color: #563359;
    flex: 1;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    opacity: 0.8;
}

.CategoryItem:hover {
    opacity: 1;
}

.CategoryItem.Active {
    opacity: 1;
}

.CategoryItem.Active p {
    font-weight: 800;
}

.CategoryItem p {
    font-weight: 600;
    color: #563359;
}

.CategoryItem:last-of-type {
    border-right: none;
}

.CategoryItem i {
    margin-right: 10px;
    color: #563359;
}

@media (max-width: 768px) {
    .CategoryItem p {
        display: none;
    }
}
